.NBSuper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(19, 19, 19, 0.5);
  width: 100%;
  border-bottom: solid 2px var(--color2);
  position: fixed;
  top: 0;
  z-index: 500;
}

.NBContainer {
  font-family: "Roboto Mono", monospace;
  color: var(--color1);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 120px;
  width: 80%;
  margin-top: 20px;

  /* border: solid 1px lime; */
}

.NBContAlt {
  height: 70px;
  border-bottom: solid 2px var(--color1);
  backdrop-filter: blur(5px);
}

.NBLogo {
  width: 200px;

  /* border: 1px solid red; */
}

.NBLogo img {
  transition: 0.5s;
  height: 90px;
}

.NBLogoAlt img {
  height: 50px;
}

.NBMenu {
  display: flex;
  flex-direction: column;
  z-index: 150;
  justify-content: space-around;
  align-items: baseline;
  width: 400px;
  position: fixed;
  backdrop-filter: blur(5px);
  border-radius: 3rem;
  background-color: rgba(0, 0, 0, 0.5);

  border: solid 2px var(--color3);
}

.NBMenu a {
  transition: 0.5s;
}

.NBMenu a:hover {
  transform: scale(1.2);
}

.NBSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 200px;

  /* border: 1px solid red; */
}

.Hamburger1 {
  cursor: none !important;
  width: 200px;
}

.NVLinkC {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-left: 50px;
}

.NVLinkC p {
  padding-left: 30px;
}
